import * as React from "react";
import { _t } from "../../../utils/Translator";

export interface SearchItem {
  name: string;
  id: number;
}

// TODO We make the assumption the items contain "name" and "id" keys.
// Is there a way to enforce this?
export interface SearchSelectProps {
  items: SearchItem[];
  selectedItem: SearchItem;
  onItemSelected: (i: SearchItem) => void;
  selectText: string;
  searchText: string;
  onSearch?: (query: string) => void;
}

export interface SearchSelectState {
  searchInput: string;
}

export default class SearchSelect<T extends SearchSelectProps> extends React.Component<T, SearchSelectState> {
  constructor(props) {
    super(props);
    this.state = {
      searchInput: "",
    };
  }

  getItemHTML = (item: SearchItem) => {
    return (
      <a className="dropdown-item" href="#">
        {item.name}
      </a>
    );
  };

  getItemsToShow = (items: SearchItem[]) => {
    const itemsToShow = [];
    for (const i of items) {
      if (!this.props.selectedItem || i.id !== this.props.selectedItem.id) {
        itemsToShow.push(
          <li key={i.id} onClick={() => this.props.onItemSelected(i)}>
            {this.getItemHTML(i)}
          </li>
        );
      }
    }
    if (!itemsToShow.length && this.state.searchInput.length >= 2) {
      itemsToShow.push(
        <li key="no-results" className="dropdown-item text-muted">
          {_t("No items found")}
        </li>
      );
    }
    return itemsToShow;
  };

  render() {
    const { items = [], selectedItem, selectText, searchText } = this.props;
    let itemsToShow = this.getItemsToShow(items);
    return (
      <div>
        <div className="btn-group searchselect-dropdown" role="group">
          <div className="form-select searchselect-dropdown-button" role="button" data-bs-toggle="dropdown">
            <div className="searchselect-dropdown-button-text">{selectedItem ? selectedItem.name : selectText}</div>
          </div>
          <ul className="dropdown-menu dropdown-menu-right dropdown-menu-wide">
            <li className="searchselect-input" key={"searchselect-input"}>
              <a>
                <input
                  className="dropdown-item"
                  type="text"
                  value={this.state.searchInput}
                  onChange={(e) => {
                    const value = e.target.value;
                    this.setState({ searchInput: value });
                    if (this.props.onSearch) {
                      this.props.onSearch(value);
                    }
                  }}
                  placeholder={searchText}
                />
              </a>
              <hr className="dropdown-divider"></hr>
            </li>
            {itemsToShow}
          </ul>
        </div>
      </div>
    );
  }
}
