import React, { FC, useState } from "react";
import { _t } from "../../../utils/Translator";
import SearchTagSelect from "../searchselect/SearchTagSelect";
import { TagItem } from "../tags/tag";
import Tags from "../tags/tags";
import { DocumentItem } from "./Document";
import api from "../../api";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import Error from "../error/Error";

export interface SaveDocumentProps {
  selectedCase: { id: number };
  selectedDocument: DocumentItem;
  onDocumentSelected: (p: DocumentItem) => void;
  saveNew: boolean;
  setSaveNew: (n: boolean) => void;
  newDocumentName: string;
  setNewDocumentName: (n: string) => void;
  documentTags: TagItem[];
  selectedDocumentTags: TagItem[];
  onDocumentTagSelected: (t: TagItem) => void;
  onDocumentTagDeselected: (t: TagItem) => void;
}

/* global localStorage */
const getConnectionToken = () => {
  return "Bearer " + localStorage.getItem("odooConnectionToken");
};

const SaveDocument: FC<SaveDocumentProps> = ({
  selectedCase,
  selectedDocument,
  onDocumentSelected,
  saveNew,
  setSaveNew,
  newDocumentName,
  setNewDocumentName,
  documentTags,
  selectedDocumentTags,
  onDocumentTagSelected,
  onDocumentTagDeselected,
}) => {
  const [documents, setDocuments] = useState<DocumentItem[]>([]);
  const [error, setError] = useState<string>("");

  const searchDocuments = async (query: string) => {
    if (!selectedCase?.id || !query || query.length < 2) return;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.searchDocuments,
        ContentType.Json,
        getConnectionToken(),
        {
          query,
          case_id: selectedCase.id,
        },
        true
      );
      const response = JSON.parse(await request.promise);
      if (response.result.success) {
        setDocuments(response.result.data.documents);
      } else {
        setError(response.result.error);
      }
    } catch (error) {
      setError(String(error));
    }
  };

  const getDocumentButton = () => {
    if (selectedDocument) {
      const url = `${api.baseURL}/web#id=${selectedDocument.id}&model=cases.document&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    }
    return null;
  };

  return (
    <div>
      <Error error={error} setError={setError} />
      <div className="page-section-title">
        {_t("Document")}&nbsp;{getDocumentButton()}
      </div>

      <div className="form-check">
        <input
          className="form-check-input"
          type="checkbox"
          checked={saveNew}
          onChange={(e) => setSaveNew(e.target.checked)}
        />
        <label className="form-check-label">{_t("New")}</label>
      </div>

      <div className="mt-2">
        {saveNew ? (
          <input
            className="name-input form-control"
            type="text"
            placeholder={_t("Describe document")}
            value={newDocumentName}
            onChange={(e) => setNewDocumentName(e.target.value)}
          />
        ) : (
          <SearchTagSelect
            items={documents}
            selectedItem={selectedDocument}
            onItemSelected={onDocumentSelected}
            tags={documentTags}
            selectText={_t("Select document")}
            searchText={_t("Search document")}
            onSearch={searchDocuments}
          />
        )}
      </div>

      <div className="mt-2">
        {saveNew && (
          <Tags
            tags={documentTags}
            selectedTags={selectedDocumentTags}
            onTagSelected={onDocumentTagSelected}
            onTagDeselected={onDocumentTagDeselected}
          />
        )}
      </div>
    </div>
  );
};

export default SaveDocument;
