import * as React from "react";
import { TagItem } from "../tags/tag";
import SearchSelect, { SearchItem, SearchSelectProps } from "./SearchSelect";

export interface SearchTagItem extends SearchItem {
  tagIds: number[];
}

export interface SearchTagSelectProps extends SearchSelectProps {
  items: SearchTagItem[];
  selectedItem: SearchTagItem;
  onItemSelected: (i: SearchTagItem) => void;
  tags: TagItem[];
}

export default class SearchTagSelect extends SearchSelect<SearchTagSelectProps> {
  getTagsHTML = (tags: TagItem[]) => {
    let tagElements = [];
    for (let tag of tags) {
      tagElements.push(
        <div key={tag.id} className="badge bg-primary rounded-pill me-2">
          {tag.name}
        </div>
      );
    }
    return <div className="mb-2">{tagElements}</div>;
  };

  getItemHTML = (item: SearchTagItem) => {
    let tags = this.props.tags.filter((t) => item.tagIds.includes(t.id));
    return (
      <a className="dropdown-item" href="#">
        <div>{item.name}</div>
        {this.getTagsHTML(tags)}
      </a>
    );
  };
}
