import React, { FC } from "react";
import { _t } from "../../../utils/Translator";
import Select from "../searchselect/Select";

export interface TimesheetTemplateItem {
  id: number;
  name: string;
  time: string;
}

export interface TimesheetTemplateProps {
  timesheetTemplates: TimesheetTemplateItem[];
  selectedTimesheetTemplate: TimesheetTemplateItem;
  onTimesheetTemplateSelected: (c: TimesheetTemplateItem) => void;
}

const TimesheetTemplate: FC<TimesheetTemplateProps> = ({
  timesheetTemplates,
  selectedTimesheetTemplate,
  onTimesheetTemplateSelected,
}) => {
  return (
    <div>
      <Select
        items={timesheetTemplates}
        selectedItem={selectedTimesheetTemplate}
        onItemSelected={onTimesheetTemplateSelected}
        selectText={_t("Select timesheet template")}
        searchText={_t("Search timesheet template")}
      />
    </div>
  );
};

export default TimesheetTemplate;
